// Configurable variables
// Declar variables that can beused throughout stylesheets.
// -----------------------------------------------------------------------------

// Fonts
$font-style-regular          : Font-Regular;
$font-style-bold             : Font-Bold;
$font-style-light            : Font-Light;
$font-style-italic           : Font-Italic;

$font-style-websafe          : Arial, Helvetica, sans-serif;
$font-style-default          : "HelveticaNeue-Light",
                               "Helvetica Neue Light",
                               "Helvetica Neue",
                               Helvetica,
                               Arial,
                               "Lucida Grande",
                               sans-serif;

$font-style-base             : $font-style-default;
$font-size-base              : 16px;

$font-weight-base            : 300;


// Line-height
$line-height-base            : 20px;


// Grid
$grid-width-min              : 320px;
$grid-width-max              : 1920px;

$grid-width-small            : 100%;
$grid-width-medium           : 768px;
$grid-width-large            : 1280px;
$grid-width-xlarge           : 1440px;
$grid-width-xxlarge          : 1920px;

$grid-padding-small          : 20px;
$grid-padding-medium         : 20px;
$grid-padding-large          : 20px;
$grid-padding-xlarge         : 20px;
$grid-padding-xxlarge        : 20px;

$grid-gutter-small           : 40px;
$grid-gutter-medium          : 40px;
$grid-gutter-large           : 40px;
$grid-gutter-xlarge          : 40px;
$grid-gutter-xxlarge         : 40px;

$grid-column-small           : 12;
$grid-column-medium          : 12;
$grid-column-large           : 12;
$grid-column-xlarge          : 12;
$grid-column-xxlarge         : 12;

$grid-breakpoint-small       : 320px;
$grid-breakpoint-medium      : 768px;
$grid-breakpoint-large       : 1280px;
$grid-breakpoint-xlarge      : 1440px;
$grid-breakpoint-xxlarge     : 1920px;


// Colors
$color-base                  : #666;
$color-light                 : #999;
$color-bright                : #fff;
$color-primary               : #000;
$color-highlight             : #bbb;
$color-selection             : #b3d4fc;


// Backgrounds
$bkg-color-base              : #fff;
$bkg-color-primary           : #000;
$bkg-color-highlight         : #bbb;


// Links
$link-color-normal           : #666;
$link-color-hover            : #000;
$link-color-active           : #000;
$link-bkg-normal             : #fff;
$link-bkg-hover              : #fff;
$link-bkg-active             : #fff;

